.contacts {
  position: relative;
  width: 100%;
  max-width: 1300px;
  margin: 0 auto;
  border-left: 1px solid #7e7c7c;
  border-right: 1px solid #7e7c7c;
  border-bottom: 1px solid #7e7c7c;
}
.contacts .title_session_contacts {
  font-size: 16px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 1vw;
  padding: 4.5vw 0;
}
.contacts .title_session_contacts .number_session_contacts {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 25px;
  background-color: #000;
  color: #fff;
  padding: 6px;
}
.contacts .square {
  background-color: #000;
  width: 30px;
  height: 30px;
}
.contacts .square_one {
  position: absolute;
  right: 0;
  top: 0;
}
.contacts .square_two {
  position: absolute;
  top: 30px;
  right: 30px;
}
.contacts .title_container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
.contacts .title_container .line {
  width: 100%;
  max-width: 50px;
  height: 2px;
  background-color: #000;
}
.contacts .title_container .title_contact_us {
  text-align: center;
}
.contacts .container_logo {
  cursor: pointer;
  width: 100%;
  max-width: 500px;
  max-height: 400px;
  margin: 20px auto;
  text-align: center;
  border: 2px solid #000;
  border-radius: 15px;
}
.contacts .container_logo img {
  width: 100%;
  border-radius: 15px;
  position: relative;
  left: 15px;
  top: 10px;
}